export default {
	data() {
		return {
			
		}
	},
	methods: {
		appOAuthLogin(e){
			var serverUrl = this.$common.serverUrl
			var qqID = this.$common.qqId				
			// 获取用户的登录类型
			var logintype =e.currentTarget.dataset.logintype
			// 授权登录
			uni.login({
				provider:logintype,
				success(loginres){
					// 授权登录登录成功后,获取用户信息
					uni.getUserInfo({
						provider:logintype,
						success(info) {
							// console.log(JSON.stringify(info))
							var userInfo = info.userInfo
							var face = ""
							var nickname=""
							var openIdOrUid = ""
							if(logintype== "weixin"){
								face = userInfo.avatarUrl
								nickname =userInfo.nickName
								openIdOrUid = userInfo.openId
							} else if(logintype== "qq"){
								face = userInfo.figureurl_qq_2
								nickname =userInfo.nickname
								openIdOrUid = userInfo.openid
							} else if(logintype== "sinaweibo"){		
								face = userInfo.avatar_large
								nickname =userInfo.nickname
								openIdOrUid = userInfo.id
							}
							// 调用开发者后台,执行一键注册或登录
							uni.request({
								url: serverUrl + '/appUnionLogin/' + logintype + '?'+qqID,
								data:{
									"face":face,
									"nickname":nickname,
									"openIdOrUid":openIdOrUid
								},
								method:"POST",
								success(result){										
									if(result.data.status ==200){
										var usrInfo = result.data.data
										// 保存用户信息到全局缓存中
										uni.setStorageSync('globalUser',userInfo)
										// 切换页面跳转,使用tab切换api
										uni.switchTab({
											url:"../me/me"
										})
									}
								}
							})					
						}
					})
				}
			})
		},

		// 微信小程序端的微信登录
		wxLogin(e){
			// 获取微信用户信息
			var userInfo = e.detail.userInfo
			var serverUrl = this.$common.serverUrl
			var qqID = this.$common.qqId
			uni.login({
				provider:"weixin",
				success(result){
					// 获得微信登录code,授权码
					var code = result.code 
					// 设置登录到哪个后端小程序，0-next超英预告，1-超英预告，2-next学院电影预告
					var loginToWhichMP = 1
					uni.request({
						url:serverUrl+'/stu/mpWXLogin/'+ code + "?"+ qqID,
						data:{
							"avatarUrl":userInfo.avatarUrl,
							"nickName":userInfo.nickName,
							"whichMP":loginToWhichMP
						},
						method:"POST",
						success(result){
							console.log(result)
						}
					})			
				}
			})
		},

		formSubmit(e){
			var username = e.detail.value.username
			var password = e.detail.value.password
		// 登录注册请求
		uni.request({
			url: this.$common.serverUrl+'/user/registOrLogin?'+ this.$common.qqId,
			data:{
				"username":username,
				"password":password
			},
			method:'POST',
			success: res => {
				if(res.data.status ==200){
					var userInfo = res.data.data
					// 保存用户信息到全局缓存中
					uni.setStorageSync("globalUser",userInfo)
					// 登录成功后,使用tab切换api
					uni.switchTab({
						url:"../me/me"
					})
				}
				else if(res.data.status ==500){
					uni.showToast({
						title:res.data.msg,
						duration:2000,
						image:'../../static/icon_other/error.png'
					})
				}
			}
		});				
		},
	}
}
